import React from 'react';
import './ProjectCard.css';

const ProjectCard = ({ project }) => {
    const { title, description, image, technologies, demoLink, codeLink } = project;

    return (
        <div className="project-card">
            {image && <img src={image} alt={title} className="project-image" />}
            <div className="project-content">
                <h3 className="project-title">{title}</h3>
                <p className="project-description">{description}</p>

                <div className="project-tech">
                    {technologies.map((tech, index) => (
                        <span key={index} className="tech-tag">{tech}</span>
                    ))}
                </div>

                <div className="project-links">
                    {demoLink && (
                        <a href={demoLink} target="_blank" rel="noopener noreferrer" className="project-link demo-link">
                            Live Demo
                        </a>
                    )}
                    {codeLink && (
                        <a href={codeLink} target="_blank" rel="noopener noreferrer" className="project-link code-link">
                            View Code
                        </a>
                    )}
                </div>
            </div>
        </div>
    );
};

export default ProjectCard;